nav{
    padding: 2px 1%;
    margin: 0;
    justify-content: space-between;
    max-height: 60px;
    align-items: center;
    background: #fff;
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 200;

}

.Navbar .Navbar-left .logo{
    width: 60px;

}

.Navbar .Navbar-middle .shop-icon{
    width: 25px;
    height: 40px;
    position: relative;
    margin-right: 60px;
    margin-top: 20px;
}

.Navbar .Navbar-middle .shop-icon .shop{
    width: 90%;
    height: 60%;
    position: absolute;
} 

 .Navbar .Navbar-middle .shop-icon .green-active{
    width: 1.4rem;
    height: 1.1rem;
    color: #fff;
    font-size: 0.5rem;
    font-weight: bold;
    padding: 5px;
    top: 40%;
    left: 60%;
    background: green;
    border-radius: 50%;
    position: absolute;
} 

.Navbar-middle .Search-box{
    border: 1px solid #ccc;
    margin-top: 5px;
    margin-bottom: 5px;
    padding: 10px 20px;
    border-radius: 25px;
    max-width: 800px;
}

.Navbar-middle .Search-box input{
    width: 500px;
    border: 0;
    outline: 0;
    background: transparent;
}

.Navbar-middle .Search-box img{
    width: 15px;
   
}

.Navbar-right .notify{
    width: 20px;
    margin-right: 25px;
}

.Navbar-right .user-profile{
    width: 35px;
    border: 2px ;
    border-radius: 50%;
    border: 2px solid #fcbf49;
    margin-right: 20px;
}

.Navbar-right h4{
    font-size: .8rem;
    margin-bottom: 5px;
}